@import url(~intro.js/introjs.css);
:root {
  --black: #131519;
  --black-border: #111317;
  --blue: #0045f2;
  --lightning-blue: #2a49fb;
  --gray: #1f2228;
  --dark-gray: #1a1c21;
  --blue-text: #6d69ff;
  --gray-text: #838485;
  --white-text: #fdfdfd; }

html {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Helvetica Neue", Arial, sans-serif;
  font-size: 62.5%;
  /* =10px */ }

body {
  background-color: #000;
  color: #fdfdfd;
  font-size: 1.2rem;
  /* =12px */
  height: 100%;
  line-height: 1.5rem;
  margin: 0;
  overflow: hidden;
  width: 100%; }

*,
::after,
::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

[hidden],
.hidden {
  display: none !important; }

.noselect {
  user-select: none !important; }

button {
  background-color: #131519;
  border: none;
  cursor: pointer;
  color: #fdfdfd;
  font-size: 1rem;
  outline: none;
  padding: 0.4rem 0.8rem; }
  button:hover, button:active {
    background-color: #1f2228; }
  button:disabled {
    cursor: default;
    opacity: 0.5; }

select {
  appearance: none !important;
  -ms-appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  background-color: #131519;
  background-image: url("/images/chevron-down-solid.svg");
  background-position: right center;
  background-repeat: no-repeat;
  background-size: 1.25em 10px;
  border-color: #131519;
  border-radius: 0.3rem;
  color: #f4f4f4;
  line-height: 1em;
  min-height: 20px;
  outline: none;
  padding: 0 1.5em 0 0.3em;
  text-indent: 0.01px;
  text-overflow: ""; }
  select::-ms-expand {
    display: none; }
  select > option {
    background-color: #131519; }

a {
  color: #fdfdfd;
  text-decoration: none; }
  a:hover, a:visited {
    color: #fdfdfd; }

.introjs-helperNumberLayer {
  display: none; }

.urbain-tutorial-wizard {
  background-color: #f4f4f4;
  border-radius: 0.3rem;
  color: #131519;
  font-size: 1.2rem;
  padding: 2rem;
  max-width: 580px; }
  .urbain-tutorial-wizard .introjs-tooltiptext {
    font-size: 1.2rem;
    line-height: 1.5rem;
    padding-bottom: 2rem;
    text-align: justify; }
  .urbain-tutorial-wizard .introjs-progress {
    border-radius: 2px;
    margin-bottom: 1rem; }
  .urbain-tutorial-wizard .introjs-progressbar {
    background-color: #7a9ffa; }
  .urbain-tutorial-wizard .introjs-tooltipbuttons {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between; }
  .urbain-tutorial-wizard .introjs-button {
    background-color: #131519;
    border: none;
    cursor: pointer;
    color: #fdfdfd;
    font-size: 1rem;
    outline: none;
    padding: 0.4rem 0.8rem;
    background-image: none;
    border-radius: 0.3rem;
    box-sizing: border-box;
    font-size: 1.2rem;
    font-weight: bold;
    padding: 5px 20px;
    text-shadow: none;
    text-align: center;
    width: calc(50% - 3rem / 2); }
    .urbain-tutorial-wizard .introjs-button:hover, .urbain-tutorial-wizard .introjs-button:active {
      background-color: #1f2228; }
    .urbain-tutorial-wizard .introjs-button:disabled {
      cursor: default;
      opacity: 0.5; }
  .urbain-tutorial-wizard .introjs-skipbutton {
    display: none;
    margin-right: 0; }
    .urbain-tutorial-wizard .introjs-skipbutton.introjs-donebutton {
      background-color: #3449bd;
