      .urbain-tutorial-wizard .introjs-skipbutton.introjs-donebutton:hover, .urbain-tutorial-wizard .introjs-skipbutton.introjs-donebutton:active {
        background-color: #0045f2; }
  .urbain-tutorial-wizard .introjs-prevbutton {
    background-color: #f4f4f4;
