    .urbain-tutorial-wizard .introjs-prevbutton:hover, .urbain-tutorial-wizard .introjs-prevbutton:active {
      background-color: #f4f4f4;
      color: #0045f2;
      border-color: #0045f2; }
  .urbain-tutorial-wizard .introjs-nextbutton {
    background-color: #3449bd;
    border: 1px solid #3449bd;
    color: #fdfdfd;
    order: 2; }
    .urbain-tutorial-wizard .introjs-nextbutton:hover, .urbain-tutorial-wizard .introjs-nextbutton:active {
      background-color: #0045f2; }
  .urbain-tutorial-wizard__content--wide {
    width: 400px; }
